import axios from './index'
import utils from '../utils'
// 
export const captcha = (data) => {
  return axios({
    method: 'post',
    url: '/h5/base/captcha',
    data
  })
}
// 
export const login = (data) => {
  return axios({
    method: 'post',
    url: '/h5/base/login',
    data,
    headers: {
      ak: 'hanhou-app',
      ts: Date.now(),
      sign: ''
    }
  })
}
