/* eslint-disable no-undef */
import {
  USER_INFO,
  TOKEN
} from './uniField'
import { Toast } from 'antd-mobile'

// let webUrl = ''
// if(process.env.NODE_ENV==='development') {
//   webUrl = 'localhost'
// }else if(process.env.NODE_ENV==='test') {

// }else {

// }
console.log('utils')
let timer = null
const utils = {
  isApp: () => {
    return !!App
  }, // app
  // App app
  ownApp: (success, fail) => {
    try {
      if (App) {
        return success()
      } else {
        if (fail) {
          return fail()
        } else {
          Toast.show({
            content: 'App'
          })
          setTimeout(() => {
            window.location.href = 'https://h5.yuanyinfu.com/h5/app'
          }, 2000)
        }
      }
    } catch (error) {
      if (fail) {
        fail()
      } else {
        Toast.show({
          content: 'App'
        })
        if (timer) clearTimeout(timer)
        timer = setTimeout(() => {
          window.location.href = 'https://h5.yuanyinfu.com/h5/app'
        }, 2000)
      }
    }
  },
  goLogin: () => {
    App.postMessage(JSON.stringify({
      type: 'goLogin',
      params: {}
    }))
  },

  isLogin: () => {
    utils.ownApp(() => {
      const is = App.postMessage(JSON.stringify({
        type: 'isLogin',
        params: {},
        callback: 'isLoginResult'
      }))
      if (!is) {
        this.goLogin()
      }
    }, () => {
      window.location.href = window.location.origin + `/#/login?back=${window.location.hash.replace('#', '')}`
    })
  },

  isLoginResult: (value) => {
    alert('isLoginResult: ' + value)
  },
  // 
  // shareTitle shareDesc  shareUrl shareCover
  shareWebToWX: (shareTitle, shareDesc, shareUrl, shareCover) => {
    App.postMessage(JSON.stringify({
      type: 'shareWebToWX',
      params: { shareTitle, shareDesc, shareUrl, shareCover }
    }))
  },

  openMiniProgram: (username, path) => {
    App.postMessage(JSON.stringify({
      type: 'openMiniProgram',
      params: { username, path }
    }))
  },
  // APP
  openAppPage: (pageJSON) => {
    // {"page":"myScorePage","params":{}, "isNeedLogin": true}
    // 
    // {"page":"musicDetailPage","isNeedLogin":false,"params":{"music_info_id":0}}
    App.postMessage(JSON.stringify({
      type: 'openAppPage',
      params: pageJSON
    }))
  },

  // 
  getUserInfo: () => {
    const info = localStorage.getItem(USER_INFO)
    return info ? JSON.parse(info) : null
  },
  setUserInfo: (obj) => {
    localStorage.setItem(USER_INFO, JSON.stringify(obj))
  },
  delUserInfo: () => {
    localStorage.removeItem(USER_INFO)
  },
  // token
  getToken: () => {
    const token = localStorage.getItem(TOKEN)
    return token || ''
  },
  setToken: (data) => {
    localStorage.setItem(TOKEN, data)
  },
  delToken: () => {
    localStorage.removeItem(TOKEN)
  },
  // 
  hashPush: (hash = '/', params = {}) => {
    const paramArr = []
    Object.keys(params).forEach(item => {
      paramArr.push(`${item}=${params[item]}`)
    })
    let toPath = window.location.origin + `/#${hash}`
    if (paramArr.length) {
      toPath = toPath + '?' + paramArr.join('&')
    }
    window.location.href = toPath
  },
  // URL params
  getUrlParams: (url = window.location.href) => {
    const params = {}
    if (url.includes('?')) {
      const strArr = url.split('?')
      if (strArr.length > 1) {
        const strArr1 = strArr[1].split('&')
        strArr1.forEach(item => {
          const strArr2 = item.split('=')
          params[strArr2[0]] = strArr2[1]
        })
        return params
      } else {
        return params
      }
    } else {
      return params
    }
  },
  // hash
  getHashQuery: (url = window.location.href) => {
    let hash = '/'
    if (url.includes('#')) {
      hash = url.split('#')[1]
    }
    const params = {
      path: hash,
      query: {

      }
    }
    if (hash.indexOf('?')) {
      const strArr = hash.split('?')
      params.path = strArr[0]
      if (strArr.length > 1) {
        const strArr1 = strArr[1].split('&')
        strArr1.forEach(item => {
          const strArr2 = item.split('=')
          params.query[strArr2[0]] = strArr2[1]
        })
        return params
      } else {
        return params
      }
    } else {
      return params
    }
  },
  // 
  getTimeData: (date) => {
    const time = new Date(date)
    let y = time.getFullYear()
    let mon = time.getMonth() + 1
    let dd = time.getDate()
    let hh = time.getHours()
    let MM = time.getMinutes()
    let ss = time.getSeconds()
    y = y < 10 ? `0${y}` : y
    mon = mon < 10 ? `0${mon}` : mon
    dd = dd < 10 ? `0${dd}` : dd
    hh = hh < 10 ? `0${hh}` : hh
    MM = MM < 10 ? `0${MM}` : MM
    ss = ss < 10 ? `0${ss}` : ss
    return {
      y,
      mon,
      dd,
      hh,
      MM,
      ss
    }
  }
}
export default utils
