import axios from 'axios'
import utils from '../utils/index'
import { Toast } from 'antd-mobile'
const NODE_ENV = process.env.NODE_ENV
// console.log('NODE_ENV', NODE_ENV, process.env.REACT_APP_BASE_API)
let toast = null
const instance = axios.create({
  baseURL: '/api',
  timeout: 20000,
  headers: {
    'x-token': '',
    ak: 'hanhou-app',
    sign: ''
  }
})
// 
instance.interceptors.request.use(function (config) {
  console.log('config', config)
  // 
  toast = Toast.show({
    content: '',
    duration: 0,
    maskClickable: false,
    icon: 'loading'

  })
  config.headers.ts = Date.now()
  config.headers['x-token'] = utils.getToken()
  return config
}, function (error) {
  // 
  return Promise.reject(error)
})

// 
instance.interceptors.response.use(function (response) {
  console.log('response', response)
  toast.close()
  if (response.data.code !== 0) {
    Toast.show({
      icon: 'fail',
      content: response.data.msg || 'error'
    })
    if (response.data.code === 700 || response.data.code === 701 || response.data.code === 702) {
      utils.delUserInfo()
      utils.delToken()
      // APP APPAPPH5
      utils.isLogin()
    }
  }
  // 
  return response.data
}, function (error) {
  console.log('axios error:', error)
  toast.close()
  Toast.show({
    icon: 'fail',
    content: error.message
  })
  // 
  return Promise.reject(error)
})

export default instance
